<template>
  <el-container class="field-props-container">
    <el-header v-if="!!!showingInDialog" class="field-props-header">
      [精度小数]字段属性设置
    </el-header>
    <el-main class="field-props-pane">
      <el-form
        ref="editorForm"
        :model="fieldProps"
        :rules="rules"
        label-position="left"
        label-width="220px"
        size="mini"
        @submit.native.prevent
      >
        <el-form-item label="字段名称" prop="name">
          <el-input
            v-model="fieldProps.name"
            :disabled="fieldState !== 1"
          ></el-input>
        </el-form-item>
        <el-form-item label="显示名称" prop="label">
          <el-input v-model="fieldProps.label"></el-input>
        </el-form-item>
        <el-form-item label="小数精度位数（不超过6位）">
          <el-input-number
            v-model="fieldProps.num_length"
            :min="0"
            :max="6"
            style="width: 100%"
          ></el-input-number>
        </el-form-item>
        <!-- <el-form-item label="最小值">
          <el-input
            v-model.number="fieldProps.fieldViewModel.minValue"
            type="number"
            style="width: 100%"
          ></el-input>
        </el-form-item>
        <el-form-item label="最大值">
          <el-input
            v-model.number="fieldProps.fieldViewModel.maxValue"
            type="number"
            style="width: 100%"
          ></el-input>
        </el-form-item> -->
        <el-form-item
          label="字段校验函数(可多选)"
          prop="fieldViewModel.validators"
        >
          <el-select
            v-model="verifiable"
            multiple
            allow-create
            filterable
            default-first-option
            :popper-append-to-body="false"
            style="width: 100%"
          >
            <el-option
              v-for="(vt, vtIdx) in validators"
              :key="vtIdx"
              :label="vt.label"
              :value="vt.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="是否主键">
          <el-radio-group
            v-model="fieldProps.is_mainfield"
            style="float: right"
          >
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否允许空值">
          <el-radio-group v-model="fieldProps.is_null" style="float: right">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="允许修改字段">
          <el-radio-group v-model="fieldProps.is_edit" style="float: right">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>

        <hr style="border: 0; border-top: 1px dotted #cccccc" />
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            style="width: 120px"
            @click="saveField"
          >
            保存字段
          </el-button>
          <el-button v-if="!!showingInDialog" size="medium" @click="cancelSave">
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
// import { addField } from '@/api/system-manager'
import FieldState from "@/views/system/field-state-variables";
import { fieldEditorMixin } from "@/views/system/field-editor/field-editor-mixin";
import { saveField } from "@/api";

export default {
  name: "DecimalWidgetEditor",
  mixins: [fieldEditorMixin],
  props: {
    entity: Object,
    fieldName: String,
    showingInDialog: Boolean,
    fieldState: {
      type: Number,
      default: FieldState.NEW,
    },
  },
  data() {
    return {
      fieldProps: {
        data_code: "",
        default_show: 0,
        facttable_id: "",
        field_length: 0,
        field_sql: "",
        file_path: "",
        file_size: 0,
        id: "",
        image_path: "",
        is_edit: 0,
        is_mainfield: 0,
        is_null: 0,
        is_system: 0,
        label: "",
        name: "",
        num_length: 0,
        order_number: 0,
        table_name: "",
        type: 2,
        verifiable: "",
      },
      verifiable: [],
      validators: [],
    };
  },
  mounted() {
    //mixin
  },
  methods: {
    async saveField() {
      let validResult = true;
      this.fieldProps.table_name = this.entity.name;
      this.fieldProps.facttable_id = this.entity.id;
      this.fieldProps.verifiable = this.verifiable.join(",");
      this.$refs["editorForm"].validate((success) => {
        if (!success) {
          validResult = false;
          return false;
        }
      });
      if (!validResult) return;
      const { state, msg } = await saveField(this.fieldProps);
      this.$message({
        message: msg,
        type: state,
      });
      this.$emit("fieldSaved");
      // this.fieldProps.type = "Integer";
      // if (this.fieldState === FieldState.NEW) {
      //   this.createNewField();
      // } else if (this.fieldState === FieldState.EDIT) {
      //   this.modifyOldField();
      // } else {
      //   // error
      // }
    },

    cancelSave() {
      this.$emit("cancelSave");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles/form-layout/field-editor-common.scss";
</style>
